
import './index.css'
import {useEffect} from 'react'

import screenshot from './latest.png'
import {Link} from 'react-router-dom'

const SingleProject = () => {

    useEffect( () => {
        document.querySelector(".home").addEventListener("scroll",  handleScroll)
    }, [])


    const handleScroll = e => {
        const pos = e.target.scrollTop
        const height = window.innerHeight / 2

        const project = document.querySelector(".project-image")
        const hasClass = project.classList.contains("show")

        if( !hasClass && pos >= height && pos !== 0 ) {
            project.classList.add("show")
        }
        else if( hasClass && pos <= height && pos !==  window.innerHeight) {
            project.classList.remove("show")
        }

        console.log(pos, height)
    }


    return (
        <div className='single-project'>

            <div className='project-image'>
                <img alt='screen shot' src={screenshot}  loading="lazy"/>
            </div>


            <div className='project-description'>
                <h2>Latest Project</h2>

                <p>
                    For my latest project I recreated the original version of Reddit which allows users to post links, and vote/comment on posts. Ruby on Rails was used for the database, controllers, routing, and views. I Built an adaptable SQL database, which, during deployment, was easily changed from SQLite to a PostgreSQL database, and used Active Record to create schemas, migrations, and relations.
                </p>

                <Link to="/projects">🗄 See all my projects</Link>
            </div>
        </div>
    )
}

export default SingleProject