
import './index.css'
import {useEffect} from 'react'
import {Link, useLocation} from 'react-router-dom'
import resume from './resume.pdf'

const Navigation = () => {
    const location = useLocation()
    const path = location.pathname

    const home = path === '/'
    const projects = path === '/projects'
    const about = path === '/about'

    useEffect( () => {
        window.scrollTo( 0, 0 )
    }, [path])

    return (
        <div className='nav-container'>

            <nav>
                <Link className={home? 'active': ''} to="/">Home</Link>
                <Link className={projects? 'active': ''} to="/projects">Projects</Link>
                <Link className={about? 'active': ''} to="/about">About</Link>

                <a target="blank" href={resume}> Resume </a>
            </nav>

        </div>
    )
}

export default Navigation